import React, { Component } from "react";
import axios from 'axios';

import PostForm from './PostForm';

class Post extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      comment: '',
      ApiData: [], //APIから取得したデータ
      RawData: [], //入力されたデータ
      ErrorMsg: ''
    }
  }

  componentDidMount = () => {
    axios
    .get('https://jsonplaceholder.typicode.com/comments')
    .then(res => {
      this.setState({
        ApiData: res.data
      });
    })
  }

  onSubmit = e => {
    e.preventDefault();
    const name = this.state.name;
    const comment = this.state.comment;
    if(name !== '' && comment !== '') {
      this.state.RawData.push({
        name: name,
        comment: comment
      });
      this.setState({
        name: '',
        comment:'',
        ErrorMsg: '',
      });
    } else {
      this.setState({
        name: name,
        comment: comment,
        ErrorMsg: '名前とコメントは必ず入力して下さい。'
      });
    }
  }

  onChange = e => {
    e.preventDefault();
    if(e.target.name === 'name') {
      this.setState({
        name: e.target.value
      });
    } else if(e.target.name === 'comment') {
      this.setState({
        comment: e.target.value
      });
    }
  }
 
  render() {
    //レンダリング時にAPIで取得したデータ
    const ApiData = this.state.ApiData.map((apiData, id) => {
      return (
        <article key={apiData.id} className="text-center">
          <h5>@{apiData.name}</h5>
          <p>{apiData.body}</p>
          <br />
          <hr />
        </article>
      );
    });

    //エラーメッセージ
    const ErrorMsg = (
      <div className="text-center">
        <p className="text-danger">{this.state.ErrorMsg}</p>
      </div>
    );

    //入力されたデータ
    const RawData = this.state.RawData.map((rawData, id) => {
      return (
        <article key={rawData.id} className="text-center">
          <h5>@{rawData.name}</h5>
          <p>{rawData.comment}</p>
          <br />
          <hr />
        </article>
      );
    });

    return (
      <div className="text-center">
        <h1 className="text-success">簡易掲示板</h1>
        {ErrorMsg}
        <PostForm 
          name={this.state.name}
          comment={this.state.comment}
          onChange={this.onChange}
          onSubmit={this.onSubmit}
        />
        {RawData}
        <p>↓過去の記事↓</p>
        <hr />
        {ApiData}
      </div>
    );
  }
}

export default Post;